<template lang="html">
  <div class="">
    <default-head header="汽车积分"></default-head>
    <div id="wrapper" class="page-cnt-box">
      <nav-bar @tab-handle="tabHandle" lefttext="积分兑换汽车爱心" righttext="汽车爱心兑换天使爱心"></nav-bar>
      <div  v-show="sign == 'left'">
        <div class="text-box" >
          <flm-form-input label="可用积分余额" textAlign="right"></flm-form-input>
        </div>
        <div class="text-box" >
          <flm-form-input label="兑换数量" placeholder="请输入您要兑换的天使爱心数量" textAlign="right"></flm-form-input>
        </div>
        <div class="text-box" >
          <flm-form-input type="password" label="可用积分余额" placeholder="请输入支付密码" textAlign="right"></flm-form-input>
        </div>
      </div>
      <div  v-show="sign == 'right'">
        <div class="text-box" >
          <flm-form-input label="可用积分余额" textAlign="right"></flm-form-input>
        </div>
        <div class="text-box" >
          <flm-form-input label="兑换数量" placeholder="请输入您要兑换的天使爱心数量" textAlign="right"></flm-form-input>
        </div>
        <div class="text-box" >
          <flm-form-input type="password" label="可用积分余额" placeholder="请输入支付密码" textAlign="right"></flm-form-input>
        </div>
      </div>
      <div class="point-for-notice">
        <div class="point-for-notice__left">兑换比例: </div>
        <div class="point-for-notice__right">1 : 1 (1积分 = 1个天使积分)</div>
      </div>
      <sub-button class="area-foot-btn" text="确定兑换" marginTop="1rem"></sub-button>
    </div>
  </div>
</template>

<script>
import defaultHead from '@/components/head/defaultHead'
import subButton from '@/components/common/subButton'
import navBar from '@/components/angle/navBar'
export default {
  components: { defaultHead, subButton, navBar },
  data: function () {
    return {
      sign: 'left'
    }
  },
  methods: {
    tabHandle: function (sign) {
      this.sign = sign
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../assets/css/variable.less';
.page-cnt-box {
  .text-box {
    background: @whiteColor;
    padding: 0 .22rem;
    border-bottom: 1px solid @lineColor;
  }
  .text-box:last-child {
    border-bottom: none
  }
}
.point-for-notice {
  // overflow: hidden;
  height: .88rem;
  line-height: .88rem;
  font-size: .3rem;
  padding: 0 .22rem;
}
.point-for-notice__left {
  float: left;
  margin-right: .2rem;
}
.point-for-notice__right {
  overflow: hidden;
  color:@redColor;
}
</style>



