<template lang="html">
  <div class="nav-bar">
      <div class="nav-bar-item" @click="tabHandle('left')">
          <span :class="{'active': sign === 'left'}" class="leftText">{{lefttext}}</span>
      </div>
      <div class="nav-bar-item" @click="tabHandle('right')">
          <span :class="{'active': sign === 'right'}" class="rightText">{{righttext}}</span>
      </div>
  </div>
</template>

<script>
export default {
  props: {
    lefttext: {
      type: String,
      default: '左侧'
    },
    righttext: {
      type: String,
      default: '右侧'
    }
  },
  data () {
    return {
      sign: 'left'
    }
  },
  methods: {
    tabHandle (sign) {
      this.sign = sign
      this.$emit('tab-handle', sign)
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../assets/css/variable.less';
.nav-bar {
  width: 100%;
  height: .88rem;
  background: @whiteColor;
}
.nav-bar .nav-bar-item {
  width: 50%;
  height: .88rem;
  line-height: .88rem;
  font-size: .32rem;
  text-align: center;
  float: left;
}
.nav-bar .nav-bar-item span {
  display: block;
  width: 100%;
  height: 100%;
  border-bottom: 1px solid @lineColor;
}
.nav-bar .nav-bar-item span.active {
  color: @navBarRed;
  border-bottom: 2px solid @navBarRed;
}
</style>



